export interface IImageSettings {
    viewports: {
        xs: IImageDimension;
        sm?: IImageDimension;
        md?: IImageDimension;
        lg?: IImageDimension;
        xl?: IImageDimension;
    };
    lazyload?: boolean;
    disableLazyLoad?: boolean;
    quality?: number;
    cropFocalRegion?: boolean;
}

export interface IImageDimension {
    w: number;
    h: number;
    q?: string;
}
